.buttonPage {
  margin-left: 10px;
  padding: 10px 20px 10px 20px;
  color: antiquewhite;
  background-color: black;
  border-radius: 5px;
  cursor: pointer;
  @media screen and (max-width: 560px) {
    display: none;
  }
}

.active {
  animation: all 1s;
  padding: 12px 22px 12px 22px;
  background-color: antiquewhite;
  color: black;
}